import React, { Component } from 'react'
import "./style/style.scss";
import dataed from "@/config/tableList/"
import {Table} from 'antd';
import { observer, inject } from 'mobx-react';
@inject('AllList')
@observer
class Visited extends Component {
    state={
        brr:[
            {
                title:"新增拜访客户"
            },
            {
                title:"取消拜访客户"
            }
        ],
        ind:0
    }
    render() {
        let {ind,brr} = this.state
        let {AllList} = this.props;
        let {allList} = AllList;
        allList = allList.map(item=>{return item});
        return (
            <div className="wrap-visited">
                <div className="visited-top">
                    {
                      brr.map((item,index)=><span
                          className={index===ind ? 'active' :''}
                          onClick={()=>{
                                    this.setState({
                                        ind:index
                                    })
                              }} key={index}>
                                    {item.title}
                                </span>)
                    }
                </div>
                <div className="visited-bottom">
                     <Table dataSource={allList} columns={dataed.customerVisited} pagination={false}/>;
                </div>
            </div>
        )
    }
}
export default Visited
